"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import GaugeChart from "react-gauge-chart";
import { db, auth } from "@/lib/firebase";
import { doc, getDoc, collection, query, orderBy, limit, getDocs } from "firebase/firestore";

export default function StudentDashboard() {
  const [latestStress, setLatestStress] = useState(null);
  const [historical, setHistorical] = useState([]);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  const userId = auth.currentUser?.uid;
  const email = auth.currentUser?.email;

  useEffect(() => {
    if (!userId) return;

    const fetchData = async () => {
      try {
        // Fetch latest stress from stressLevels
        const docRef = doc(db, "stressLevels", userId);
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
          setLatestStress(docSnap.data().stressLevel);
        }

        // Fetch last 5 historical mood entries
        const moodQuery = query(
          collection(db, "moodEntries"),
          orderBy("timestamp", "desc"),
          limit(5)
        );

        const querySnapshot = await getDocs(moodQuery);
        const entries = querySnapshot.docs
          .map((d) => d.data())
          .filter((entry) => entry.uid === userId);
        setHistorical(entries);
      } catch (err) {
        console.error("Error fetching dashboard data:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [userId]);

  // Determine emoji for latest stress
  const getEmoji = (stress) => {
    if (stress < 4) return "ðŸ˜Š";
    if (stress < 7) return "ðŸ˜";
    return "ðŸ˜¢";
  };

  if (!userId) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p>Please login to access dashboard.</p>
      </div>
    );
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p>Loading...</p>
      </div>
    );
  }

  return (
    <div className="min-h-screen p-8 bg-gray-100 flex flex-col gap-8">
      <h1 className="text-3xl font-bold">Welcome, {email}</h1>

      {/* Latest Stress */}
      {latestStress !== null && (
        <div className="bg-white p-6 rounded-xl shadow-md flex flex-col items-center gap-4">
          <h2 className="text-2xl font-semibold">Today's Stress Level</h2>
          <div className="text-6xl">{getEmoji(latestStress)}</div>
          <GaugeChart
            id="latest-gauge"
            nrOfLevels={20}
            colors={[
              "#00FF00", "#33FF00", "#66FF00", "#99FF00",
              "#CCFF00", "#FFFF00", "#FFCC00", "#FF9900",
              "#FF6600", "#FF3300", "#FF0000"
            ]}
            percent={latestStress / 10}
            arcPadding={0.02}
            needleColor="#333333"
            needleBaseColor="#333333"
            animate={true}
            formatTextValue={() => `${latestStress} / 10`}
          />
        </div>
      )}

      {/* Historical Entries */}
      <div className="bg-white p-6 rounded-xl shadow-md">
        <h2 className="text-2xl font-semibold mb-4">Recent Stress Logs</h2>
        {historical.length === 0 ? (
          <p>No previous entries found.</p>
        ) : (
          <ul className="space-y-2">
            {historical.map((entry, idx) => (
              <li key={idx} className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
                <span>{new Date(entry.timestamp.seconds * 1000).toLocaleDateString()}</span>
                <span className="flex items-center gap-2">
                  {entry.stressLevel} / 10 {getEmoji(entry.stressLevel)}
                </span>
              </li>
            ))}
          </ul>
        )}
      </div>

      <button
        onClick={() => router.push(`/student/stress-meter?uid=${userId}`)}
        className="w-full bg-indigo-600 hover:bg-indigo-700 text-white font-bold py-3 px-4 rounded-xl shadow-lg transition-transform transform hover:scale-105"
      >
        Log Today's Stress
      </button>
    </div>
  );
}
